import styles from "./../Modal.module.css";
import {SubmitHandler, useForm} from "react-hook-form";
import {NewQuery, postNewQueryThunk} from "../../../../redux/modal-reducer";
import {useAppDispatch} from "../../../../redux/hooks";
import {useState} from "react";

const QueryModal = () => {
    const dispatch = useAppDispatch();
    let [isLegalEntity, setIsLegalEntity] = useState(false);

    const {
        register,
        formState: {errors, isValid},
        handleSubmit,
        reset
    } = useForm<NewQuery>();

    const onSubmit: SubmitHandler<NewQuery> = (data) => {
        dispatch(postNewQueryThunk(data));
        reset({
            name: '',
            email: '',
            phoneNumber: '',
            isLegalEntity: false,
            comment: ''
        })
        setIsLegalEntity(false);
    }

    return (
        <div className={styles.queryModalContent}>
            <h2 className={styles.queryTitle}>
                <span>Оставьте заявку</span> <br/>
                и мы с Вами свяжемся!
            </h2>
            <div className={styles.formWrapper}>
                <form onSubmit={handleSubmit(onSubmit)} className={styles.form}>
                    <label className={styles.label}>
                        <input className={styles.input} type="text" {...register('name', {
                            required: "Ошибка. Поле не заполнено"
                        })} placeholder="Ваше имя"/>
                        {errors?.name && <p className="errorMessage">{errors?.name?.message || "Ошибка заполнения"}</p>}
                    </label>
                    <label className={styles.label}>
                        <input className={styles.input} type="email"
                               placeholder="Email (обязательно)" {...register('email')}/>
                    </label>
                    <label className={styles.label}>
                        <input className={styles.input} type="tel"
                               placeholder="Номер телефона" {...register('phoneNumber')}/>
                    </label>
                    <label className={styles.label}>
                        <input className={styles.input} type="text"
                               placeholder="Ваш вопрос или комментарий" {...register('comment')}/>
                    </label>
                    <label htmlFor="checkbox" className={styles.label} onChange={() => setIsLegalEntity(!isLegalEntity)}>
                        <input type="checkbox" id="checkbox" {...register('isLegalEntity')}/>
                        <span className="custom-checkboxes-span"></span>
                        <span className={styles.checkboxText}>
                        Вы юридическое лицо?
                    </span>
                    </label>
                    {
                        isLegalEntity &&
                        <label className={styles.label}>
                            <input className={styles.input} type="text"/>
                        </label>
                    }
                    <label className={styles.label}>
                        <input type="submit" value={"Оставить заявку"}/>
                    </label>
                </form>
            </div>
        </div>
    )
}

export default QueryModal;